.App {
  text-align: center;
}
.App-header {
  top: 0;
  text-align:center;
  background-color: #ddd;
  margin: 0px;
}
.App-title-box{
  vertical-align: middle;
  padding: 0px;
}

.App-title {
  top: 0px;
  font: 55px Arial, sans-serif;
  font-weight: bold;
}

.App-logo  {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 62px;
  width: 62px;
}

.main-form  {
  text-align:left;
}

#main-table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

#main-table td, #main-table th {
  border: 1px solid #ddd;
  padding: 8px;
}

#main-table tr:nth-child(even){background-color: #f2f2f2;}

#main-table tr:hover {background-color: #ddd;}

#main-table th {
  position: sticky;
  top: 0;
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #04AA6D;
  color: white;
}
